.App {
  overflow: hidden;
  font-family: sans-serif;
}

.App-header {
  /* background: rgb(200, 235, 250);
  background: linear-gradient(90deg,
      rgb(192, 235, 253) 0%,
      rgba(200, 235, 250, 1) 33%,
      rgba(255, 255, 255, 1) 100%); */
  align-items: center;
  font-size: calc(10px + 2vmin);
  padding: 10px 20px;
}

.Logo {
  position: relative;
  height: 80px;
  width: 80px;
}

.Logo-aura {
  height: 80px;
  width: 80px;
  pointer-events: none;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 2;
}

@media (prefers-reduced-motion: no-preference) {
  .Logo-aura {
    animation: Logo-aura-spin infinite 20s linear;
  }
}

.Logo-teeth {
  height: 33px;
  width: 31px;
  pointer-events: none;
  position: absolute;
  left: 24px;
  top: 23px;
  z-index: 1;
}

.App-link {}

.Slogan {
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  padding: 20px;
  line-height: 1em;
}

.Slogan h1 {
  color: rgb(22,86,159);
  font-size: 23px;
}

.Slogan h2{
  color: rgb(91,191,241);
  font-size: 14px;
}

@keyframes Logo-aura-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}